@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
.slider{height:90vh;margin:0;font-family:"Poppins",sans-serif}.slider .slide{position:relative}.slider .linkContent{position:absolute}.slider .linkContent .titleSlide{font-size:50px;margin:10px;color:#fff;text-shadow:1px 1px 0 #22140c}.slider .swiper-slide{text-align:center;font-size:18px;display:flex;justify-content:center;align-items:center}.slider .swiper-container{width:100%;height:100%}.slider .swiper-slide img{display:block;width:100%;height:100%;object-fit:cover}
button{position:relative;display:inline-block;cursor:pointer;outline:none;border:0;vertical-align:middle;text-decoration:none;font-size:12px;font-family:inherit;opacity:.5}button.LearnMore{border:2px solid transparent;color:#382b22;text-transform:uppercase;padding:1.25em 2em;background:#fff;border-radius:2px;transform-style:preserve-3d;transition:transform 150ms cubic-bezier(0, 0, 0.58, 1),background 150ms cubic-bezier(0, 0, 0.58, 1)}button.LearnMore::before{position:absolute;content:"";width:100%;height:100%;top:0;left:0;right:0;bottom:0;background:transparent;border-radius:inherit;box-shadow:0 0 0 2px transparent,0 .625em 0 0 transparent;transform:translate3d(0, 0.75em, -1em);transition:transform 150ms cubic-bezier(0, 0, 0.58, 1),box-shadow 150ms cubic-bezier(0, 0, 0.58, 1)}button.LearnMore:hover{color:#fff;border:2px solid;background:rgba(180,159,158,.51)}button.LearnMore:hover::before{box-shadow:0 0 0 2px transparent,0 .5em 0 0 transparent;transform:translate3d(0, 0.5em, -1em)}button.LearnMore:active::before{box-shadow:0 0 0 2px transparent,0 0 transparent;transform:translate3d(0, 0, -1em)}
*{padding:0;margin:0}.container{font-family:Helvetica,sans-serif;color:#000}
